$secondColor: #0076CE;


.carxchange-modal {
  *,
  ::after,
  ::before {
    box-sizing: border-box;
  }
  dl,
  ol,
  ul {
    margin-top: 0;
    margin-bottom: 1rem;
  }
  lottie-player {
    max-width: 100% !important;
  }
  button:not(.back-arrow), .btn:not(.back-arrow) {
    cursor: pointer;
    border: 0;
    display: flex;
    padding: 12px 24px;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    border-radius: 6px;
    background-color: #127bbf;
    transition: background-color .3s;
    color: #fff;
    font-size: 14px;
    line-height: 24px;
    font-weight: 500;
    text-align: left;
    letter-spacing: .5px;
    text-decoration: none;
    margin: auto;
    min-width: 255px;
    &:hover {
      background-color: #0f649b;
      color: #fff;
    }


    @media (max-width: 400px) {
      min-width: unset !important;
      width: 100%;
    }

    &:disabled {
      opacity: .65;
      cursor: default;
    }
  }
  &.search-opened {
    .search-field {
      z-index: 10001
    }

    .modal-body:after {
      opacity: 1
    }
  }

  .radio-wrapper {
    .label-wrapper {
      position: relative;

      label {
        user-select: none;
        margin: 0;
        padding: 0 10px;
        position: relative;
        color: #999;
        border: solid 2px rgba(0, 0, 0, 0.1);
        text-align: center;
        cursor: pointer;
        height: 80px;
        display: flex;
        font-weight: 700;
        font-size: 13px;
        align-items: center;
        justify-content: center;
        background: white;
        border-radius: 10px;

        @media (max-width: 650px) {
          height: 70px;
        }
      }

      input {
        position: absolute;
        opacity: 0;
        cursor: pointer;
        width: 0;
        display: none;
      }

      input:checked+label,
      label:hover {
        transition: all .3s ease !important;
        border: 2px solid $secondColor !important;

        .text {
          color: $secondColor;
        }
      }

      .icon {
        display: none;
      }

      input:checked~.icon {
        display: block;
        position: absolute;
        color: $secondColor;
        top: -10px;
        right: -10px;
        transform: none;
        background: white;
        @media (max-width: 400px) {
          right: 0;
        }
      }

      .svg {
        display: block;
        margin-bottom: 10px;
      }

      img {
        max-width: 100%;
        max-height: 56px;

        @media (max-width: 650px) {
          max-height: 36px;
          width: auto;
        }
      }

      .text {
        font-size: 13.7px;
        font-weight: 400;
        margin-bottom: 0px;
        @media (max-width: 576px) {
          font-size: 12.7px;
        }
      }
    }
  }

  .container {
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
    padding-top: 62px;
    padding-bottom: 50px;
    max-width: 1160px;
    position: relative;
    

    @media (max-width: 991px) {
      padding-top: 20px;
    }
  }

  .col-holder {
    min-height: calc(100vh - 230px);
    display: flex;
    align-items: flex-start !important;

    @media (max-width: 992px) {
      margin-top: 1rem;
    }

    .v-list {
      width: 170px;
      display: none;
      user-select: none;

      @media (min-width: 992px) {
        display: block;
      }
      .open-modal-car {
        cursor: pointer;
      }
      .item {
        border: 2px solid rgba(0, 0, 0, 0.1);
        width: 100%;
        cursor: pointer;
        margin-bottom: 10px;
        font-size: 14px;
        color: black;
        background: white;
        border-radius: 10px;
        padding: 15px 20px;
        font-weight: 700;

        &.active {
          border-color: rgba(114, 114, 114, 0.5);
        }

        &:not(.fade-item) {
          display: none;
        }

        label {
          display: block;
          margin: 0 0 4px;
          line-height: 1;
          color: #727272;
          font-size: 12px;
        }
      }
    }

    .tab-content {
      flex: 1 0 0%;
      align-self: normal;

      @media (max-width: 991px) {
        width: 100%;
      }
    }

    .slide {
      display: none;
      height: 100%;

      &.active {
        display: block;
      }

      @media (max-width: 991px) {
        margin: 0 !important;
        width: 100%;
      }

      @media (min-width: 992px) {
        width: 712px;
        margin-left: 104px;
      }

      @media (max-width: 1199px) {
        margin-left: 25px;
      }

      .select-body-type {
        transition: all 0.3s ease;
        transform: translateX(-400px);
        opacity: 0;
        overflow: hidden;
        margin-top: 3rem !important;
        display: none;

        .label-wrapper {
          label {
            flex-direction: column;
            height: auto;
            padding: 10px;
            line-height: 1;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
          }
        }

        &.active {
          opacity: 1;
          transform: translateX(0);
          position: relative;
        }
      }

      &:first-child,
      &:nth-child(2) {
        .slide-header {
          margin-bottom: 2rem;
        }
      }

      .slide-header {
        display: flex;
        margin-bottom: 1rem;
        align-items: center;
        justify-content: space-between;
        @media (max-width: 576px) {
          flex-direction: column;
          align-items: flex-start;
          .search-field {
            margin-top: 24px;
            width: 100%;
          }
        }
      }
    }
  }

  .progress {
    background: rgba(52, 85, 249, 0.1);
    
    position: sticky;
    position: -webkit-sticky;
    z-index: 30;
    display: flex;
    height: 8px;
    overflow: hidden;
    font-size: .75rem;
    border-radius: 0;

    .progress-bar {
      display: flex;
      overflow: hidden;
      transition: width .6s ease;
      background: #127bbf;
    }
  }

  h5 {
    font-size: 18px;
    margin: 0px;
    line-height: 20px;
    font-weight: 700;
    color: black;
    outline: none;
  }
  .slide-thanks {
    .header {
      margin: 0 auto;
      padding: 60px 0 0;
      max-width: 820px;
      text-align: center;
      h2 {
        margin: 0;
        font-size: 3.5rem;
        font-weight: 700;
        line-height: 1.1;
        text-transform: uppercase;
        -webkit-hyphens: auto;
        -moz-hyphens: auto;
        -ms-hyphens: auto;
        hyphens: auto;
        @media (max-width: 576px) {
          font-size: 3rem;
        }
      }
    }
    .main-content {
      text-align: center;
      margin: 50px auto 0 auto;
      max-width: 820px;
    }
    .main-content__body {
      text-align: center;
      margin: 20px 0 0;
      font-size: 1.8rem;
      line-height: 1.4;
    }
  }
  .search-field {
    z-index: 10001;
    position: relative;

    @media (min-width: 1200px) {
      min-width: 260px;
    }

    @media (min-width: 992px) {
      min-width: 392px;
    }

    input[type="search"] {
      color: rgba(0, 0, 0, 0.50);
      font-size: 14px;
      padding-inline-start: 50px !important;
      border-radius: 50px;
      height: 50px;
      width: 100%;
      box-shadow: 0px 0px 7px rgba(0, 0, 0, 0.1);
      border: 1px solid rgba(0, 0, 0, 0.1);
      padding: 0.375rem 20px;
      line-height: 1.5;
      background-clip: padding-box;
      transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;

      &:focus {
        border-color: $secondColor;
        outline: none;
        box-shadow: none;
      }

      &::placeholder {
        color: rgba(0, 0, 0, 0.50);
        opacity: 0.5;
      }
    }

    .search-icon {
      position: absolute;
      top: 25px;
      transform: translateY(-50%);
      left: 20px;
      cursor: pointer;
      width: 18px;
      height: 18px;

      .search-path {
        stroke-width: 2px;
      }
    }

    .makes-drop-down {
      width: 100%;
      border-radius: 5px;
      max-height: 50vh;
      overflow-y: auto;
      z-index: 1000;
      display: none;
      position: absolute;
      top: 100%;
      left: 0;
      padding: 0;
      margin: 0.125rem 0 0;
      font-size: 1rem;
      color: #212529;
      text-align: left;
      list-style: none;
      background-color: #fff;
      background-clip: padding-box;
      border: 1px solid rgba(0, 0, 0, 0.15);

      &.opened {
        display: block;
      }

      li {
        display: block;
        width: 100%;
        padding: 0.5rem 1rem;
        clear: both;
        font-weight: 400;
        color: #212529;
        text-align: inherit;
        font-size: 14px;
        border-bottom: solid 1px rgba(0, 0, 0, 0.1);
        white-space: nowrap;
        background-color: transparent;
        cursor: pointer;
      }

      .match {
        font-weight: 600;
        color: green;
      }
    }
  }

  .car-makes {
    display: grid;
    grid-gap: 13px;
    grid-template-columns: repeat(auto-fit, minmax(126px, 1fr));

    @media (max-width: 767px) {
      .label-wrapper:only-child {
        max-width: 150px;
      }
    }
  }

  .cross-icon {
    cursor: pointer;
    width: 40px;
    height: 40px;
    border-radius: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(0, 0, 0, 0.05);

    @media (min-width: 992px) {
      position: absolute;
      top: 62px;
      right: 15px;
    }

    @media (max-width: 991px) {
      width: 30px;
      height: 30px;
      border-radius: 30px;
    }
  }
  
  .mileage-price {

    .text-center {
      text-align: center !important;
    }
  
    .block {
      margin-bottom: 1.5rem;
  
      &:first-child {
        margin-bottom: 3rem;
        padding-bottom: 1.5rem;
      }
  
      .slider-holder {
        width: 100%;
  
        .title-row {
          display: flex;
          font-size: 24px;
          align-items: center;
          justify-content: space-between;
          padding-bottom: 44px;
          color: #000;
          font-weight: 700;
  
          &:first-child {
            font-size: 18px
          }
        }
  
        .labels-holder {
          max-width: 402px;
          font-size: 14px;
          padding-top: 30px;
          margin: auto;
          display: flex;
          justify-content: space-between;
  
          span {
            position: relative;
            min-width: 50px;
            white-space: nowrap;
            line-height: 1.3;
            display: flex;
            flex-direction: column;
            align-items: center;
            text-align: center;
            color: #666666;
            font-weight: 700;
  
            &::after {
              content: attr(data-label);
            }
          }
        }
      }
  
      .range-full-alert {
        background: #F9F9F9;
        display: none;
        max-width: 447px;
        width: 100%;
        margin: 0 auto 20px;
        padding: 20px;
        text-align: center;
        margin-top: 28px;
  
        h6 {
          font-size: 16px;
          margin-bottom: 16px;
          margin-top: 0;
          color: black;
          font-weight: 700;
        }
  
        .input-holder {
          margin-bottom: 1rem !important;
          position: relative;
  
          input {
            height: 50px;
            width: 100%;
            box-shadow: 0px 0px 7px rgba(0, 0, 0, 0.1);
            border-radius: 50px;
            padding-right: 25px;
            border: 1px solid rgba(0, 0, 0, 0.1);
            padding: 0.375rem 20px;
            font-size: 1rem;
            line-height: 1.5;
            color: #495057;
            background-clip: padding-box;
            transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
            padding-left: 65px;
          }
  
          .icon {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            left: 10px;
            display: flex;
            align-items: center;
            justify-content: flex-end;
            line-height: 1;
            height: 40px;
            width: 40px;
            border-radius: 40px;
          }
        }
      }
    }
  }
  
  .seller-info-holder {
    width: 100%;
    max-width: 550px;
    display: grid;
    grid-gap: 45px;
    padding-bottom: 20px;
    @media (max-width: 576px) {
      padding: 0;
    }
  
    p {
      margin: 0;
      text-align: center;
      color: #000000;
    }

    h5 {
      font-size: 18px;
      margin: 0 0 17px;
      line-height: 20px;
      font-weight: 700;
      color: black;
      outline: none;
      @media (max-width: 576px) {
        margin: 0 !important;
      }
    }
    
    .grid-holder {
      display: grid;
      grid-gap: 25px;
      grid-template-columns: auto;
  
      .item {
        textarea {
          min-height: 90px;
          border-radius: 25px;
          display: block;
          padding: 15px 25px;
          resize: none;
          border: 1px solid #BCCCDC;
          box-shadow: none;
          font-size: 1rem;
          line-height: 1.5;
          color: #495057;
          width: 100%;
        }
  
        .control-label {
          font-size: 16px;
          display: block;
          line-height: 18px;
          margin-bottom: 8px;
          color: #000000;
          font-weight: 700;
        }
  
        .grid-holder[grid-item-width-sm="1/2"] {
          @media (min-width: 576px) {
            grid-template-columns: repeat(auto-fill, minmax(calc((100% / 2) - 15px), 1fr));
          }
        }
  
        .input-holder {
          position: relative;

          .error {
            border-color: #C0392B !important;
            border-width: 2px !important;
            &:focus {
              outline-color: #C0392B;
            }
          }
  
          select, #datepicker {
            appearance: none;
            padding-right: 45px;
            background: #fff;
            border: 1px solid #BCCCDC;
            box-shadow: none;
            padding: 0.375rem 20px;
            font-size: 1rem;
            line-height: 1.5;
            height: 50px;
            width: 100%;
            color: black;
            border-radius: 50px;
  
            &::placeholder {
              color: rgba(0, 0, 0, 0.50);
              opacity: 0.5;
            }
  
            option {
              color: #102A43 !important;
  
              &:first-child {
                color: #829AB1 !important;
              }
            }
          }
  
          &[data-icon] {
            input {
              padding-left: 65px !important;
            }
  
            .icon {
              position: absolute;
              top: 50%;
              transform: translateY(-50%);
              left: 10px;
              display: flex;
              align-items: center;
              justify-content: flex-end;
              line-height: 1;
              height: 40px;
              width: 40px;
              border-radius: 40px;
            }
          }

          &[data-tel] {
            input {
              padding-left: 108px !important;
            }
  
            &::after {
              content: attr(data-tel);
              color: black;
              line-height: 18px;
              position: absolute;
              top: 51%;
              transform: translateY(-50%);
              left: 58px;
            }
          }
          .icon {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            right: 20px;
            pointer-events: none;
          }
        }
  
        input:not(#datepicker) {
          position: relative;
          border: 1px solid #BCCCDC;
          box-shadow: none;
          height: 50px;
          width: 100%;
          border-radius: 50px;
          padding: 0.375rem 20px;
          font-size: 1rem;
          line-height: 1.5;
          color: #495057;
          background-clip: padding-box;
          padding-left: 45px;
  
          &::placeholder {
            color: rgba(0, 0, 0, 0.50);
            opacity: 0.5;
          }
        }
  
        .city-field {
          position: relative;
  
          .location-icon {
            position: absolute;
            pointer-events: none;
            top: 14px;
            left: 15px;
            z-index: 9;
          }
          ul {
            position: absolute;
            top: calc(100% + 12px);
            left: 0;
            width: 100%;
            border-radius: 20px;
            z-index: 100;
            margin: 0;
            box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
            padding: 35px 32px 0;
            background: #fff;
            max-height: 300px;
            overflow-y: auto;
            &:after {
              content: attr(data-placeholder);
              position: absolute;
              color: #999;
              top: 20px;
              left: 32px;
              font-size: 13px;
              font-weight: 700;
            }
            li {
              border-bottom: 1px solid rgba(0, 0, 0, 0.1);
              cursor: pointer;
              list-style-type: none;
              display: flex;
              align-items: center;
              padding: 10px 0;
              color: rgba(0, 0, 0, 0.7);
              min-height: 60px;
            }
          }
        }
      }
    }
  }
  .modal-header {
    justify-content: space-between;
    direction: ltr;
    padding-right: 1rem;
    align-items: center;
    height: 80px;
    position: relative;
    z-index: 11;
    border: 0;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.07);
    border-radius: 0;
    background: white;
    padding: 1rem;
    color: #444444;
    font-weight: 700;
    font-size: 18px;
    line-height: 20px;
    display: flex !important;
  
    @media (min-width: 992px) {
      display: none !important;
    }
  
    .back-arrow {
      height: 48px;
      background: none;
      border: none;
      outline: none;
      cursor: pointer;
      padding: 0;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      visibility: hidden;
    }
  
    .title {
      padding: 0 15px;
      font-size: 18px;
      font-weight: 700;
      color: #444444;
    }
  }
  
  .modal-content {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
    pointer-events: auto;
    background-color: #fff;
    background-clip: padding-box;
    outline: 0;
    background: white;
    max-height: calc(100vh - 80px);
    box-shadow: none;
    overflow: hidden;
    border-radius: 0;
    border: 0;
    height: calc(100vh - 80px);
  
    @media (max-width: 991px) {
      height: 100%;
      max-height: inherit;
    }
  }
  
  .modal {
    right: 0;
    bottom: 0;
    left: 0;
    position: fixed;
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    overflow-y: auto;
    outline: 0;
    &:not(.zoomIn):not(.zoomOut) {
      display: none !important;
    }
    @media (min-width: 992px) {
      top: 80px !important;
    }
  }
  
  .modal-open .modal {
    overflow-x: hidden;
    overflow-y: auto;
    padding-right: 0 !important;
  }
  
  @media (min-width: 576px) {
    .modal-dialog {
      max-width: 500px;
      margin: 1.75rem auto;
    }
  }
  
  .modal-dialog {
    height: 100%;
    max-width: 100%;
    margin: 0 !important;
    transition: none;
    position: relative;
    width: auto;
    margin: 0.5rem;
  }
  
  .modal-body {
    padding: 0;
    position: relative;
    flex: 1 1 auto;
    overflow-y: auto;
  
    .btn-close {
      display: none !important;
  
      @media (min-width: 992px) {
        display: flex !important;
      }
    }
  
    &::after {
      content: "";
      opacity: 0;
      pointer-events: none;
      transition: opacity 0.4s ease;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.5);
      position: fixed;
      top: 0;
      left: 0;
      z-index: 1000;
    }
  }
}
.dropdown-menu {
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 1000;
  display: none;
  float: left;
  min-width: 160px;
  padding: 5px 0;
  margin: 2px 0 0;
  font-size: 14px;
  text-align: left;
  list-style: none;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #ccc;
  border: 1px solid rgba(0,0,0,.15);
  border-radius: 4px;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.18);
}
.datepicker table tr td, .datepicker table tr th {
  text-align: center;
  width: 40px;
  height: 40px;
  border-radius: 4px;
  border: none;
}
.prev, .next {
  position: relative;
  right: unset;
  max-width: unset;
  margin: unset;
  display: flex;
}
.arrow-left::before {
  content: " ";
  border-left: 2px solid #000;
  border-bottom: 2px solid #000;
  width: 8px;
  height: 8px;
  transform: rotate(45deg);
  cursor: pointer;
  display: block;
}

.arrow-right::before {
  content: " ";
  border-right: 2px solid #000;
  border-top: 2px solid #000;
  width: 8px;
  height: 8px;
  transform: rotate(45deg);
  cursor: pointer;
  display: block;
}
.arrow-right, .arrow-left {
  display: flex;
  align-items: center;
  justify-content: center;
}
.table-condensed>tbody>tr>td, .table-condensed>tbody>tr>th, .table-condensed>tfoot>tr>td, .table-condensed>tfoot>tr>th, .table-condensed>thead>tr>td, .table-condensed>thead>tr>th {
  padding: 5px;
}

.range-slider {
  width: 402px;
  margin: auto;
  max-width: 100% !important;
}

.range-slider__range {
  -webkit-appearance: none;
  width: 100%;
  height: 6px;
  border-radius: 5px;
  background: #d7dcdf;
  outline: none;
  padding: 0;
  margin: 0;
}

.range-slider__range::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: #0076CE;
  cursor: pointer;
  -webkit-transition: background 0.15s ease-in-out;
  transition: background 0.15s ease-in-out;
}
.range-slider__range::-moz-range-thumb {
  width: 20px;
  height: 20px;
  border: 0;
  border-radius: 50%;
  background: #0076CE;
  cursor: pointer;
  -moz-transition: background 0.15s ease-in-out;
  transition: background 0.15s ease-in-out;
}

.range-slider__range::-moz-range-thumb:hover,
.range-slider__range::-webkit-slider-thumb:hover,
.range-slider__range:active::-webkit-slider-thumb,
.range-slider__range:active::-moz-range-thumb {
  background: #0076CE;
}

.range-slider__value {
  display: inline-block;
  position: relative;
  width: 60px;
  color: #fff;
  line-height: 20px;
  text-align: center;
  border-radius: 3px;
  background: #0076CE;
  padding: 5px 10px;
  margin-left: 8px;
}

.range-slider__value:after {
  position: absolute;
  top: 8px;
  left: -7px;
  width: 0;
  height: 0;
  border-top: 7px solid transparent;
  border-right: 7px solid #0076CE;
  border-bottom: 7px solid transparent;
  content: "";
}

::-moz-range-track {
  background: #eeeff4;
  border: 0;
}

input::-moz-focus-inner,
input::-moz-focus-outer {
  border: 0;
}